@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background: whitesmoke;
}

@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600;700&display=swap");
:root {
  --color-accent: rgb(112, 71, 235);
  --background-color: #f8f7fa;
  --foreground-color: #ffffff;
  --text-color: #19181a;
  --highlight-text-color: var(--text-color);
  --active-text-color: #f8f7fa;
  --inactive-text-color: #a5a5a5;
  --btn-bg: #f8f7fa;
  --box-shadow: #efefef;
  --border-width: 1px;
  --border-radius: 15px;
  --spacing: 18px;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background-color: #19181a;
    --foreground-color: #282729;
    --text-color: #c9c8cc;
    --highlight-text-color: #f8f7fa;
    --inactive-text-color: #68676a;
    --btn-bg: #313133;
    --box-shadow: #111111;
  }
}
* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
body {
  font-family: "Quicksand", sans-serif;
}
.card-calender {
  margin: 0 auto;
  /* width: 316px;
  height: fit-content; */
  background-color: var(--foreground-color);
  border-radius: var(--border-radius);
  box-shadow: 0px 0px 10px var(--box-shadow);
}
.calendar-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  padding-bottom: 15px;
  border-bottom: var(--border-width) solid var(--box-shadow);
}
.calendar-toolbar > .current-month {
  font-size: 20px;
  font-weight: bold;
  color: var(--highlight-text-color);
}
.calendar-toolbar > [class$="month-btn"] {
  width: 40px;
  aspect-ratio: 1;
  text-align: center;
  line-height: 40px;
  font-size: 14px;
  color: var(--highlight-text-color);
  background: var(--btn-bg);
  border: none;
  border-radius: var(--border-radius);
}
.weekdays,
.calendar-days {
  display: flex;
  flex-wrap: wrap;
  padding-inline: var(--spacing);
}
.weekdays {
  padding-top: 12px;
}
.calendar-days {
  padding-bottom: 12px;
}
.weekday-name,
[class$="-day"] {
  width: 40px;
  height: 40px;
  color: var(--text-color);
  text-align: center;
  line-height: 40px;
  font-weight: 500;
  font-size: 1rem;
}
.weekday-name {
  color: var(--highlight-text-color);
  font-weight: 700;
}
.current-day {
  background-color: var(--color-accent);
  color: var(--active-text-color);
  border-radius: var(--border-radius);
  font-weight: 700;
  transition: 0.5s;
  cursor: pointer;
}
.padding-day {
  color: var(--inactive-text-color);
  user-select: none;
}
.calendar-toolbar > [class$="month-btn"]:hover,
.month-day:hover,
.btn:hover {
  border-radius: var(--border-radius);
  background-color: var(--btn-bg);
  color: var(--color-accent);
  border-radius: var(--border-radius);
  transition: 0.1s;
  cursor: pointer;
  box-shadow: inset 0px 0px 0px 1.5px var(--color-accent);
}
.calendar-toolbar > [class$="month-btn"]:focus,
.month-day:focus,
.btn:focus {
  border-radius: var(--border-radius);
  background-color: var(--color-accent);
  color: var(--active-text-color);
}
.goto-buttons {
  border-top: var(--border-width) solid var(--box-shadow);
  padding-block: 18px;
  display: flex;
  justify-content: space-evenly;
}
.btn {
  background: var(--btn-bg);
  border: none;
  border-radius: var(--border-radius);
  padding: 11px 13px;
  color: var(--highlight-text-color);
  font-family: "Quicksand", sans-serif;
  font-weight: 600;
  font-size: 0.9rem;
  margin-right: 1px;
  box-shadow: 0px 0px 0px var(--box-shadow);
}
body {
  background: #fbfbfd;
}
header {
  width: 100%;
}
.pad-100 {
  padding: 75px 0;
}
.sr_header {
  width: 100%;
  position: relative;
  display: flex;
  align-items: Center;
  position: fixed;
  z-index: 9999999;
}
.sr_header section {
  width: 100%;
  overflow: hidden;
}
.sr_header section:nth-child(1) {
  width: 30%;
}
.sr_cross {
  background: #0d6efd;
  display: inline-block;
  font-size: 2em;
  padding: 0.5em 2em;
  text-decoration: none;
  position: absolute;
  left: -5%;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 39%;
}
.sr_parallelogram {
  transform: skew(-20deg);
}
.skew-fix {
  display: inline-block;
  transform: skew(20deg);
}
.sr_header .sr_logo {
  height: 88px;
}
.top-tab {
  width: 100%;
  background-color: #232331;
  display: flex;
  align-items: center;
  justify-content: Center;
  padding: 0.6rem 1rem;
  padding-left: 10rem;
}
.top-tab span {
  width: 100%;
  text-align: center;
  white-space: nowrap;
}
.top-tab span a {
  margin: 0 10px;
  color: #fff;
  white-space: nowrap;
}
.top-tab span:nth-child(1) a .fa {
  margin: 0 5px;
}
@media (max-width: 1120px) {
  .sr_header section:nth-child(1) {
    display: none;
  }
  .sr_cross {
    position: static;
  }
  .sr_header .sr_logo {
    width: 100%;
  }
  .top-tab {
    padding: 5px;
  }
  .top-tab span:nth-child(1) {
    display: none;
  }
  .top-tab span:nth-child(2) {
    display: flex;
    align-items: center;
    justify-content: Center;
  }
}
.sr_topnav {
  overflow: hidden;
  background-color: #fff;
  padding-left: 10rem;
}
.sr_topnav a {
  float: left;
  display: block;
  color: #232331;
  text-align: center;
  padding: 15px 25px;
  text-decoration: none;
  font-size: 18px;
  margin: 10px 20px;
  transition: all 0.3s;
}
.active {
  color: #96161f;
}
.sr_topnav .icon {
  display: none;
}
.dropdown {
  float: left;
  overflow: hidden;
}
.dropdown .dropbtn {
  font-size: 18px;
  border: none;
  outline: none;
  color: #232331;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 10px 20px;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border-top: 2px solid #96161f;
}
.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
  margin: 0;
}
.sr_topnav a:hover,
.dropdown:hover .dropbtn {
  box-shadow: 7px 5px 10px rgb(192 192 192 / 80%),
    -1px -3px 10px rgb(0 0 0 / 20%);
  border-radius: 10px;
}
.dropdown-content a:hover {
  background-color: #ddd;
  color: black;
}
.dropdown:hover .dropdown-content {
  display: block;
}
#res_logo {
  display: none;
}
@media screen and (max-width: 1120px) {
  .searchContainer {
    padding: 120px 0 0 0;
  }
  #res_logo {
    display: inline-block;
    width: 55px;
  }
  .sr_topnav a {
    margin: 0;
  }
  .sr_topnav a:not(:first-child),
  .dropdown .dropbtn {
    display: none;
  }
  .sr_topnav a.icon {
    float: right;
    display: block;
  }
}
@media screen and (max-width: 1120px) {
  .sr_topnav {
    padding-left: 0;
  }
  .form-control {
    width: 75%;
  }
  .sr_topnav.responsive {
    position: relative;
  }
  .sr_topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .sr_topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
    margin: 0px;
  }
  .sr_topnav.responsive #res_sr_logo {
    max-width: 200px;
    min-width: 200px;
  }
  .sr_topnav.responsive .dropdown {
    float: none;
  }
  .sr_topnav.responsive .dropdown-content {
    position: relative;
  }
  .sr_topnav.responsive .dropdown .dropbtn {
    display: block;
    width: 100%;
    text-align: left;
    margin: 0px;
  }
}
